#index-header {
  display: none;
}

#mobile-header {
  z-index: 10;
  border-bottom: solid 4px var(--main-color);
  width: 100%;
  display: block;
  position: sticky;
  top: 0;
}

#mobile-nav {
  background: linear-gradient(to right, var(--main-color), var(--third-color));
  width: 100%;
}

#mobile-nav-bar {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: .5rem 1rem;
  display: flex;
}

#mobile-logo {
  align-items: center;
  height: 100%;
  display: flex;
}

#mobile-logo img {
  border: 2px solid #fff;
  width: 130px;
  height: 100%;
  transition: all .1s;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 2rem;
}

#mobile-menu {
  background-color: var(--third-color);
  z-index: 3;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  width: 99%;
  height: 100vh;
  margin-left: .5%;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#mobile-menu-close {
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 0;
  padding-right: 3%;
  font-size: 30px;
  display: flex;
}

#mobile-menu-links {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 63%;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  cursor: pointer;
  width: 100%;
  height: 12.5%;
}

.mobile-menu-links-li-a {
  color: var(--fourth-color);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: flex;
}

#mobile-menu-links-selected {
  background-color: var(--fourth-color);
}

#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info {
  box-sizing: border-box;
  background-color: #000;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 30%;
  margin-top: 0;
  display: flex;
}

.mobile-menu-info {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 22%;
  display: flex;
}

.mobile-menu-info-div1 {
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: center;
  min-width: 30%;
  height: 100%;
  padding-left: 2%;
  display: flex;
}

.mobile-menu-info-div1 h4 {
  color: #fff;
  margin-left: 4%;
}

.mobile-menu-info-anchor {
  color: #fff;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 1%;
  text-decoration: none;
  display: flex;
  overflow-x: scroll;
}

#mobile-menu-info-social {
  box-sizing: border-box;
  border: 2px solid #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30%;
  margin-top: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.mobile-menu-info-social-li {
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}

.mobile-menu-info-social-li2 {
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.mobile-menu-info-social-li2-img {
  width: 20px;
  height: 20px;
}

#about-us-main {
  display: none;
}

#about-us-main-mobile {
  box-sizing: border-box;
  background-color: var(--fourth-color);
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

#about-us-main-mobile-h1 {
  text-align: center;
  background-color: var(--third-color);
  width: 75%;
  color: var(--fourth-color);
  text-decoration: underline;
  text-decoration-color: var(--third-color);
  border-radius: 8px;
  padding: 3% 0;
  font-size: 24px;
}

.about-us-main-mobile-article {
  border: 2px solid var(--third-color);
  background-color: #fff;
  border-radius: 10px;
  width: 95%;
  margin-bottom: 2%;
}

.about-us-main-mobile-h2 {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 3%;
  font-size: 20px;
}

.about-us-main-mobile-content {
  color: var(--third-color);
  height: 100%;
  padding: 4%;
  font-size: 16px;
  line-height: 1.5;
}

.about-us-main-mobile-ul {
  margin: 1rem 0;
  padding-left: 1.2rem;
}

.about-us-main-mobile-ul li {
  margin-bottom: 6px;
}

.about-us-main-mobile-link {
  background-color: var(--fourth-color);
  color: var(--main-color);
  border-radius: 6px;
  padding: 4px 8px;
  text-decoration: none;
  display: inline-block;
}

.about-us-main-mobile-link:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#index-footer {
  display: none;
}

#index-footer-mobile {
  color: #fff;
  box-sizing: border-box;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 0;
  padding: 2%;
  display: flex;
}

#index-footer-mobile-div1 {
  text-align: center;
}

#index-footer-mobile-p1 {
  margin: 0;
  font-size: 13px;
  line-height: 1.4;
}

#index-footer-mobile-div2 {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

#index-footer-mobile-p2 {
  margin: 0;
  font-size: 13px;
}

#index-footer-mobile-a1 {
  color: #fff;
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 13px;
  text-decoration: underline #fff;
  transition: all .3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#index-footer-mobile-div3 {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

#index-footer-mobile-p2 {
  color: var(--fourth-color);
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  display: flex;
}

#index-footer-mobile-p2:hover {
  color: #fff;
}
