/* MAIN HEADER */
  #index-header {
    display: none;
  }

  #mobile-header {
    position: sticky;
    top: 0;
    z-index: 10;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}


#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}

/* about us main mobile content  */
/* MAIN CONTAINER */
#about-us-main{
  display: none;
}
#about-us-main-mobile {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  box-sizing: border-box;
  background-color: var(--fourth-color);
}

/* PAGE TITLE */
#about-us-main-mobile-h1 {
  width: 75%;
  text-align: center;
  padding: 3% 0;
  font-size: 24px;
  background-color: var(--third-color);
  color: var(--fourth-color);
  text-decoration: underline;
  text-decoration-color: var(--third-color);
  border-radius: 8px;
}

/* ARTICLE CARD */
.about-us-main-mobile-article {
  width: 95%;
  background-color: white;
  border-radius: 10px;
  border: 2px solid var(--third-color);
  margin-bottom: 2%;
}

/* ARTICLE HEADER */
.about-us-main-mobile-h2 {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 3%;
  font-size: 20px;

}

/* ARTICLE CONTENT */
.about-us-main-mobile-content {
  padding: 4%;
  font-size: 16px;
  line-height: 1.5;
  color: var(--third-color);
  height: 100%;
}

/* LISTS */
.about-us-main-mobile-ul {
  padding-left: 1.2rem;
  margin: 1rem 0;

}

.about-us-main-mobile-ul li {
  margin-bottom: 6px;
}

/* LINKS */
.about-us-main-mobile-link {
  text-decoration: none;
  background-color: var(--fourth-color);
  color: var(--main-color);
  padding: 4px 8px;
  border-radius: 6px;
  display: inline-block;
}

.about-us-main-mobile-link:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* FOOTER */
#index-footer{
  display: none;
}
#index-footer-mobile {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 0px;
    padding: 2%;
}

#index-footer-mobile-div1 {
  text-align: center;

}

#index-footer-mobile-p1 {
  font-size: 13px;
  line-height: 1.4;
  margin: 0;
  
}


#index-footer-mobile-div2 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2 {
  font-size: 13px;
  margin: 0;
}

#index-footer-mobile-a1 {
  font-size: 13px;
  color: white;
  text-decoration: underline;
  text-decoration-color: white;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* ADMIN */
#index-footer-mobile-div3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: var(--fourth-color);
}
#index-footer-mobile-p2:hover{
  color: white;
}







